<script>
export default {
  name: 'params',
  data () {
    return {
      // 
      cateList: [],
      // 
      cateProps: {
        // 
        value: 'cat_id',
        // 
        label: 'cat_name',
        // 
        children: 'children'
      },
      // ID
      selectKeys: [],
      // 
      // 
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      editdialogVisible: false,
      // 
      addDialogVisible: false,
      // 
      addform: {
        attr_name: ''
      },
      // 
      addformrules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //  
      editForm: {
        attr_name: ''
      },
      // 
      editFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
      // // 
      // addynamicvisible: false,
      // // 
      // addstaticdialogVisible: false,
      // // 
      // addynamicform: [],
      // // 
      // addstaticForm: []

    }
  },
  created () {
    // 
    this.getCateList()
  },
  methods: {
    // 
    async getCateList () {
      // 
      const { data: res } = await this.$http.get('categories')
      // 
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      this.cateList = res.data
      console.log(this.cateList)
    },
    //  
    // 
    parentCateChanged () {
      this.getParamsData()
    },
    // tab 
    handleTabClick () {
      this.getParamsData()
      // console.log(this.activeName)
    },
    // 
    async getParamsData () {
      // ID
      // console.log(this.manyTableData)
      // 
      if (this.selectKeys.length !== 3) {
        this.$message.error('')
        // 
        this.selectKeys = []
        this.manyTableData = []
        this.onlyTableData = []
      }
      // 
      // ID 
      // ,many only 
      const { data: res } = await this.$http.get(`categories/${this.cateID}/attributes`, { params: { sel: this.activeName } })
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      // res.data
      // item
      // res.data item
      res.data.forEach(item => {
        // item,split
        // ? ,,
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // ,
        //  
        // inputVisibleinputValue
        item.inputVisible = false
        // 
        item.inputValue = ''
      })
      console.log(res.data)
      // 
      if (this.activeName === 'many') {
        // 
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 
    async editPar (id) {
      // 
      this.editdialogVisible = true
      // 
      // this.cateIDID idID
      const { data: res } = await this.$http.get(`categories/${this.cateID}/attributes/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      console.log(res.data)
      // v-model
      this.editForm = res.data
    },
    // 
    async submitPar () {
      this.$refs.editFormRef.validate(async valid => {
        // 
        if (!valid) return
        // cat_id  ID editForm
        const { data: res } = await this.$http.put(`categories/${this.editForm.cat_id}/attributes/${this.editForm.attr_id}`, { attr_sel: this.activeName, attr_name: this.editForm.attr_name })
        console.log(res)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.getParamsData()
        // 
        this.editdialogVisible = false
      })
    },
    // ID
    async deletePar (id) {
      console.log(id.attr_id)
      // 
      const res = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
        // catch,
      }).catch(err => err)
      //  confirm
      //  cancel
      console.log(res)
      if (res !== 'confirm') {
        this.$message.info('')
      }
      // id.cat_id ID
      // id.attr_id ID
      const { data: result } = await this.$http.delete(`categories/${id.cat_id}/attributes/${id.attr_id}`, { params: { attr_sel: this.activeName } })
      if (result.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      this.getParamsData()
    },
    // 
    adddialogClose () {
      this.$refs.addformRef.resetFields()
    },
    // 
    addParams () {
      // 
      this.$refs.addformRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(`categories/${this.cateID}/attributes`, {
          attr_name: this.addform.attr_name,
          attr_sel: this.activeName
        })
        console.log(res.data)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.getParamsData()
        // 
        this.addDialogVisible = false
      })
    },
    showInput (row) {
      row.inputVisible = true
      // 
      // $nextTick ,,
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // ,
    async handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        // ,
        row.inputValue = ''
        // 
        row.inputVisible = false
        return
      }
      // return ,,
      row.attr_vals.push(row.inputValue.trim())
      // 
      row.inputValue = ''
      // 
      row.inputVisible = false
      // 
      const { data: res } = await this.$http.put(`categories/${this.cateID}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        // 
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // attr_vals
    async saveAtterVals (row) {
      // 
      const { data: res } = await this.$http.put(`categories/${this.cateID}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        // 
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    handleClose (i, row) {
      // 
      row.attr_vals.splice(i, 1)
      this.saveAtterVals(row)
    }
  },
  // 
  computed: {
    // true false
    isBtnDisabled () {
      if (this.selectKeys.length !== 3) {
        return true
      }
      return false
    },
    // ID
    cateID () {
      if (this.selectKeys.length === 3) {
        // selectKeys 2
        return this.selectKeys[2]
      }
      return null
    },
    // 
    titleText () {
      if (this.activeName === 'many') {
        return ''
      }
      return ''
    },
    // 
    label () {
      if (this.activeName === 'many') {
        return ''
      }
      return ''
    }
  }
}

</script>

