<template>
<div>
<!---->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
  <!---->
  <el-card>
<!---->
    <el-row :gutter="20">
      <el-col span="7">
        <el-input placeholder="" v-model="queryInfo.query" clearable @change="getUserList">
        <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
      </el-input>
      </el-col>
      <el-col span="4">
        <el-button type="primary" @click="addDialogVisible = true"></el-button>
      </el-col>
    </el-row>
<!-- -->
<!--data-->
    <el-table :data="userlist" stripe border>
<!--prop-->
<!--type="index"-->
      <el-table-column type="index"></el-table-column>
      <el-table-column label="" prop="username"></el-table-column>
      <el-table-column label="" prop="email"></el-table-column>
      <el-table-column label="" prop="mobile"></el-table-column>
      <el-table-column label="" prop="role_name"></el-table-column>
      <el-table-column label="" prop="mg_state">
<!--scopr -->
        <template slot-scope="scope">
<!--{{scope.row}}-->
<!--{{scope.row.mg_state}}-->
          <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" width="180px">
        <template slot-scope="scope">
          <!---->
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
          <!---->
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
          <!---->
          <el-tooltip effect="dark" content="" placement="top" :enterable="false">
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="selectrole(scope.row)"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
<!---->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
<!---->
<!--el-dialog @close,-->
  <el-dialog
    title=""
    :visible.sync="addDialogVisible"
    width="50%" @close="addDialogClosed">
<!---->
<!--prop -->
    <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
      <el-form-item label="" prop="username">
        <el-input v-model="addForm.username"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input v-model="addForm.password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="addForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="mobile">
        <el-input v-model="addForm.mobile"></el-input>
      </el-form-item>
    </el-form>
      <!---->
    <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false"> </el-button>
<!--addDialogVisible = false-->
    <el-button type="primary" @click="addUser"> </el-button>
  </span>
  </el-dialog>
<!---->
  <el-dialog
    title=""
    :visible.sync="editDialogVisible"
    width="50%"
    @close="editDialogClose">
    <!--:model="editForm" data()editForm-->
    <!--rules-->
    <!--ref -->
    <!--disabled-->
    <!--prop -->
    <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
      <el-form-item label="">
        <el-input v-model="editForm.username" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="editForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="mobile">
        <el-input v-model="editForm.mobile"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
<!--editDialogVisible = false-->
    <el-button type="primary" @click="editUserInfo"> </el-button>
    </span>
  </el-dialog>
<!---->
  <el-dialog
    title=""
    :visible.sync="selectroledialogVisible"
    width="50%"
  @close="setRoleDialogCloseed">
    <div>
      <p>{{userInfo.username}}</p>
      <p>{{userInfo.role_name}}</p>
      <p>
<!--v-model -->
<!--selectedRoleId-->
        <el-select v-model="selectedRoleId" placeholder="">
          <el-option
            v-for="item in roleList"
            :key="item.id"
            :label="item.roleName"
            :value="item.id">
          </el-option>
        </el-select></p>
    </div>
    <span slot="footer" class="dialog-footer">
    <el-button @click="selectroledialogVisible = false"> </el-button>
    <el-button type="primary" @click="saveRoleInfo"> </el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: 'users',
  // 
  data () {
    // 
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      // true, 
      if (regEmail.test(value)) {
        // 
        return cb()
      }
      // false  
      cb(new Error(''))
    }
    // 
    var checkMobile = (rule, value, cb) => {
      // value cb 
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      // true .
      if (regMobile.test(value)) {
        return cb()
      }
      // ,
      cb(new Error(''))
    }
    return {
      // 
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10
      },
      // 
      userlist: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      editDialogVisible: false,
      // 
      selectroledialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // ID 
      editForm: {},
      // 
      addFormRules: {
        username: [
          // trigger  required
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '3~4', trigger: 'blur' }
        ],
        password: [
          // trigger  required
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '3~4', trigger: 'blur' }
        ],
        email: [
          // trigger  required
          { required: true, message: '', trigger: 'blur' }, { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' }, { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 
      editFormRules: {
        email: [
          // trigger  message
          { required: true, message: '', trigger: 'blur' },
          // email
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          // trigger  message
          {
            required: true, message: '', trigger: 'blur'
          },
          // mobile
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 
      userInfo: {},
      // 
      roleList: [],
      // ID
      selectedRoleId: ''
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    async getUserList () {
      const { data: res } = await this.$http.get('users', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // userlist 
      this.userlist = res.data.users
      console.log(this.userlist)
      // total 
      this.total = res.data.total
      console.log(res)
    },
    // pageSize 
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    //   
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      // 
      this.getUserList()
    },
    // 
    async userStateChanged (userinfo) {
      // data,res
      const { data: res } = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
      // res
      // console.log(res)
      // console.log(userinfo.mg_state)
      // 200,200,
      if (res.meta.status !== 200) {
        // 
        userinfo.mg_state = !userinfo.mg_state
        console.log(userinfo.mg_state)
        return this.$message.error('')
      }
      // 
      this.$message.success('')
      console.log(res.meta.msg)
    },
    addDialogClosed () {
      // $refs.addFormRef resetFields 
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser: function () {
      this.$refs.addFormRef.validate(async valid => {
        // 
        if (!valid) return
        // post data.  res
        const { data: res } = await this.$http.post('users', this.addForm)
        // 201 , 201  ,
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.addDialogVisible = false
        // 
        this.addDialogClosed()
      })
    },
    // 
    async showEditDialog (id) {
      // 
      this.editDialogVisible = true
      // ID
      console.log(id)
      // IDget
      const { data: res } = await this.$http.get('users/' + id)
      //  200
      if (res.meta.status !== 200) { return this.$message.error('') }
      // 200 
      this.editForm = res.data
      console.log(this.editForm)
    },
    // 
    editUserInfo: function () {
      this.$refs.editFormRef.validate(async valid => {
        // 
        // console.log(valid)
        if (!valid) return
        //  put
        const { data: res } = await this.$http.put('users/' + this.editForm.id, {
          email: this.editForm.email, mobile: this.editForm.mobile
        })
        console.log(this.editForm.id)
        if (res.meta.status !== 200) { return this.$message.error('') }
        this.$message.success('')
        // 
        this.editDialogVisible = false
        this.editDialogClose()
        // 
        this.getUserList()
      })
    },
    // 
    editDialogClose () {
      // 
      this.$refs.editFormRef.resetFields()
    },
    // 
    async selectrole (userInfo) {
      // 
      this.selectroledialogVisible = true
      this.userInfo = userInfo
      // 
      const { data: res } = await this.$http.get('roles')
      console.log(userInfo)
      console.log('selectorle')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.roleList = res.data
    },
    // ID
    async removeUserById (id) {
      // 
      const res = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
        // catch,
      }).catch(err => err)
      //  confirm
      //  cancel
      console.log(res)
      // 
      if (res !== 'confirm') {
        return this.$message.info('')
      }
      const { data: result } = await this.$http.delete('users/' + id)
      if (result.meta.status !== 200) { return this.$message.error('') }
      // 
      this.$message.success('')
      // 
      this.getUserList()
    },
    // 
    async saveRoleInfo () {
      // 
      if (!this.selectedRoleId) {
        return this.$message.error('')
      }
      // 
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.selectedRoleId
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      //  
      this.getUserList()
      // 
      this.selectroledialogVisible = false
    },
    // 
    setRoleDialogCloseed () {
      // ID  
      this.selectedRoleId = ''
      this.userInfo = {}
    }
  }
}
</script>
<style lang="less" scoped>
.text {
  font-size: 14px;
}
.item {
  padding: 18px 0;
}
.box-card {
  width: 480px;
}
.el-pagination{
  margin-top: 15px;
}
</style>
