<template>
<div>
<!---->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
<!---->
  <el-card>
    <el-row>
      <el-col>
<!---->
        <el-button type="primary" @click="addCategory()"></el-button>
      </el-col>
    </el-row>
<!---->
    <tree-table style="margin-top: 15px"
      class="treeTable"
      :data="cateList"
      :columns="columns"
      :selection-type="false"
      :expand-type="false"
      show-index
      index-text="#"
      border
      :show-row-hover="false">
<!---->
      <template slot="isok" slot-scope="scope">
      <i class="el-icon-check" v-if="scope.row.cat_deleted === false" style="color:springgreen"></i>
      <i class="el-icon-close" v-if="scope.row.cat_deleted === true" style="color: red"></i>
      </template>
<!-- -->
      <template slot="order" slot-scope="scope">
        <el-tag v-if="scope.row.cat_level === 0" size="mini"></el-tag>
        <el-tag type="success" v-if="scope.row.cat_level === 1" size="mini"></el-tag>
        <el-tag type="warning" v-if="scope.row.cat_level === 2" size="mini"></el-tag>
      </template>
<!---->
      <template slot="opt" slot-scope="scope">
        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.cat_id)"></el-button>
        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removecateById(scope.row.cat_id)" ></el-button>
      </template>
    </tree-table>
    <!---->
    <!--size-change-->
    <!--current-change-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="querInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="querInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
<!---->
  <el-dialog
    title=""
    :visible.sync="editDialogVisible"
    width="50%">
    <el-form :model="editForm" ref="editFormRef" label-width="100px">
      <el-form-item label="">
        <el-input v-model="editForm.cat_name"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editCate"> </el-button>
  </span>
  </el-dialog>
<!---->
  <el-dialog
    title=""
    :visible.sync="addCategorydialogVisible"
    width="50%"
    @close="closeCateaddForm">
<!---->
<!--:model v-model -->
<!--rules -->
<!--ref -->
<!--v-model -->
    <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
      <el-form-item label="" prop="cat_name">
        <el-input v-model="addCateForm.cat_name"></el-input>
      </el-form-item>
      <el-form-item label="">
<!--options -->
<!--Pros -->
<!--v-model  ID-->
<!--props -->
<!--clearable -->
<!--change-on-select  false-->
        <el-cascader
          v-model="selecteKeys"
          :options="feater"
          :props="casccaderPros"
          clearable
          @change="parentCateChanged"
        change-on-select>
        </el-cascader>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="addCategorydialogVisible = false"> </el-button>
    <el-button type="primary" @click="addCate"> </el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: 'categories',
  // 
  data () {
    return {
      // 
      querInfo: {
        type: 1,
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      //  
      cateList: [],
      // 
      total: 0,
      // table
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'isok'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'order'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'opt'
        }
      ],
      // 
      editDialogVisible: false,
      // 
      addCategorydialogVisible: false,
      // 
      addCateForm: {
        // 
        cat_name: '',
        // ID
        cat_pid: 0,
        // 
        cat_level: 0
      },
      // 
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //  
      feater: [],
      // 
      casccaderPros: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // ID
      selecteKeys: [],
      // I 
      editForm: {}
    }
  },
  created () {
    this.getcateList()
    // 
    this.getParentList()
  },
  methods: {
    // 
    async getcateList () {
      const { data: res } = await this.$http.get('categories', { params: this.querInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // cateList
      // this.$message.success('')
      // res.data result .result
      this.cateList = res.data.result
      console.log(this.cateList)
      // total
      this.total = res.data.total
    },
    //  
    handleSizeChange (newSize) {
      this.querInfo.pagesize = newSize
      //  
      this.getcateList()
    },
    //  
    handleCurrentChange (newPage) {
      this.querInfo.pagenum = newPage
      // 
      this.getcateList()
    },
    // 
    async showEditDialog (id) {
      // 
      this.editDialogVisible = true
      const { data: res } = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      console.log(res.data)
      this.editForm = res.data
      console.log(this.editForm.cat_name)
    },
    // 
    async editCate () {
      // 
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        //  put
        const { data: res } = await this.$http.put('categories/' + this.editForm.cat_id, {
          cat_name: this.editForm.cat_name
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.getcateList()
        // 
        this.editDialogVisible = false
      })
    },
    // ID
    async removecateById (id) {
      console.log(id)
      // 
      const res = await this.$confirm(',?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      // ,,, info('')
      console.log(res)
      // cancel
      // confirm
      if (res !== 'confirm') {
        return this.$message.info('')
      }
      const { data: result } = await this.$http.delete('categories/' + id)
      if (result.meta.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
      // 
      this.getcateList()
    },
    // 
    async addCategory () {
      // 
      this.getParentList()
      this.addCategorydialogVisible = true
    },
    // 
    async getParentList () {
      const { data: res } = await this.$http.get('categories', {
        params: {
          type: 2
        }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 200 feater
      this.feater = res.data
      console.log(this.feater)
    },
    // 
    // 
    parentCateChanged () {
      console.log(this.selecteKeys)
      // selectedKeys  (lenth) 0 , 
      // 
      if (this.selecteKeys.length > 0) {
        // ID addCateFormcat_pid 
        this.addCateForm.cat_pid = this.selecteKeys[this.selecteKeys.length - 1]
        // 1
        this.addCateForm.cat_level = this.selecteKeys.length
      } else {
        // 
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 
    async addCate () {
      // 
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.getcateList()
        // 
        this.addCategorydialogVisible = false
      })
      console.log(this.addCateForm)
    },
    // 
    closeCateaddForm () {
      // addCateFormRef 
      this.$refs.addCateFormRef.resetFields()
      // selectID
      this.selecteKeys = []
      // addCateFormcat_level  cat_pid 0
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    }
  }
}
</script>
<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
