<template>
<div>
<!-- -->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
<!---->
  <el-card>
    <el-row :gutter="20">
<!---->
      <el-col :span="8">
<!--, @changegetgoodsList -->
<!--v-modelinput-->
        <el-input placeholder="" v-model="queryInfo.query" clearable @change="getgoodsList">
<!--slot="append "-->
          <el-button slot="append" icon="el-icon-search" @click="getgoodsList"></el-button>
        </el-input>
      </el-col>
<!---->
      <el-col :span="4">
<!-- -->
        <el-button type="primary" @click="goAddpage"></el-button>
      </el-col>
    </el-row>
<!---->
<!--:data -->
    <el-table
      :data="goodsList"
      style="width: 100%" border stripe>
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="goods_name" label=""></el-table-column>
      <el-table-column prop="goods_price" label="()" width="95px"></el-table-column>
      <el-table-column prop="goods_number" label="" width="70px"></el-table-column>
      <el-table-column prop="add_time" label="" width="140px">
        <template slot-scope="scope">
          {{scope.row.add_time | dataFormat}}
        </template>
      </el-table-column>
      <el-table-column label="" width="130px">
<!---->
        <template slot-scope="scope">
<!---->
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="editGoods(scope.row.goods_id)"></el-button>
<!---->
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteGoods(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
<!---->
<!--size  handleSizeChange , -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
<!---->
  <el-dialog
    title=""
    :visible.sync="adddGoodsialogVisible"
    width="50%">
    <span></span>
    <span slot="footer" class="dialog-footer">
    <el-button @click="adddGoodsialogVisible = false"> </el-button>
    <el-button type="primary" @click="adddGoodsialogVisible = false"> </el-button>
  </span>
  </el-dialog>
<!---->
  <el-dialog
    title=""
    :visible.sync="editGoodsdialogVisible"
    width="50%">
    <span></span>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editGoodsdialogVisible = false"> </el-button>
    <el-button type="primary" @click="editGoodsdialogVisible = false"> </el-button>
  </span>
  </el-dialog>

</div>
</template>

<script>
export default {
  name: 'goods',
  data () {
    return {
      // 
      queryInfo: {
        // 
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10
      },
      // 
      goodsList: [],
      // 
      total: 0,
      // 
      adddGoodsialogVisible: false,
      // 
      editGoodsdialogVisible: false
    }
  },
  created () {
    this.getgoodsList()
  },
  methods: {
    // 
    async getgoodsList () {
      const { data: res } = await this.$http.get('goods', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      //  goodsList
      this.goodsList = res.data.goods
      console.log(res.data)
      // total ,
      this.total = res.data.total
    },
    //   returnquerypagesize
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getgoodsList()
    },
    //    returnquerypagenum
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getgoodsList()
    },
    // ID
    async deleteGoods (row) {
      const result = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      // result 
      //  confirm
      //  cancel
      if (result !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: res } = await this.$http.delete('goods/' + row.goods_id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      this.getgoodsList()
    },
    // input
    async editGoods (id) {
      // 
      this.editGoodsdialogVisible = true
      // ID
      const { data: res } = await this.$http.get('goods/' + id)
      console.log(res.data)
    },
    // 
    goAddpage () {
      // push goods/add
      this.$router.push('/goods/Add')
    },
    search (qwe) {
      console.log(qwe)
    }
  }
}
</script>

