<template slot-scope="scope">
<div>
<!---->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
<!---->
  <el-row :gutter="20">
    <el-col span="4">
      <el-button type="primary" @click="addroleDialogVisible = true"></el-button>
    </el-col>
  </el-row>
<!---->
  <el-card>
    <el-table :data="roleList" stripe border>
<!--prop,-->
<!---->
      <el-table-column type="expand">
        <template slot-scope="scope">
          <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
<!---->
            <el-col :span="5" >
              <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
              <!---->
              <i class="el-icon-caret-right"></i>
            </el-col>
<!---->
            <el-col :span="19">
<!--for -->
              <el-row :class="[i2 === 0 ? '' : 'dbtop' ]" v-for="(item2,i2) in item1.children" :key="item2.id">
                <el-col :span="6">
                  <el-tag type="success" closable @close="removeRightById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                  <!---->
                  <i class="el-icon-caret-right" ></i>
                </el-col>
<!--for-->
                <el-col :span="18">
                  <el-tag type="warning" v-for="(item3) in item2.children" :key="item3.id" closable @close="removeRightById(scope.row,item3.id)">{{item3.authName}}</el-tag>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
<!--          <pre>{{scope.row}}</pre>-->
        </template>
      </el-table-column>
      <!--type="index"-->
<!--data -->
<!--label  -->
<!--prop -->
      <el-table-column type="index"></el-table-column>
      <el-table-column label="" prop="roleName"></el-table-column>
      <el-table-column label="" prop="roleDesc"></el-table-column>
      <el-table-column label="" prop="level" width="300px">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button>
          <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteEditDialog(scope.row.id)"></el-button>
          <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
<!---->
<!--el-dialog @close-->
<!--model -->
<!--prop-->
<!--ref-->
<!--v-model input-->
  <el-dialog
    title=""
    :visible.sync="addroleDialogVisible"
    width="50%" @close="addDialogClose" rules="addFormRules">
    <el-form :model="addForm" ref="addFormRole" :rules="addFormRules" label-width="70px">
      <el-form-item label="" prop="roleName">
        <el-input v-model="addForm.roleName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="roleDesc">
        <el-input v-model="addForm.roleDesc"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="addroleDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addRole"> </el-button>
  </span>
  </el-dialog>
<!---->
<!--v-model input-->
<!--ref-->
<!--prop-->
  <el-dialog
    title=""
    :visible.sync="editDialogVisible"
    width="50%"
    @close="editDialogClose">
    <el-form :model="editForm" ref="editFormRef" :rules="addFormRules" label-width="70px">
      <el-form-item label="" prop="roleName">
        <el-input v-model="editForm.roleName" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="roleDesc">
        <el-input v-model="editForm.roleDesc"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editRoleInfo"> </el-button>
  </span>
  </el-dialog>
<!---->
  <el-dialog
    title=""
    :visible.sync="showSetRightdialogVisible"
    width="50%"
  @close="SetRightDialogClosed">
<!---->
<!--props -->
<!--:data -->
<!--show-checkbox node-key="id" -->
<!--default-expand-all="true" -->
<!--default-expand-all="true" -->
<!--defKeys -->
  <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all="true" :default-checked-keys="defKeys" ref="treeRef"></el-tree>
    <span slot="footer" class="dialog-footer">
    <el-button @click="showSetRightdialogVisible = false"> </el-button>
    <el-button type="primary" @click="allotRights"> </el-button>
  </span>
  </el-dialog>
</div>
</template>
<script>
export default {
  name: 'roles.vue',
  data () {
    return {
      // 
      roleList: [],
      // 
      addroleDialogVisible: false,
      // 
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      // 
      addFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 5, message: '25', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 5, message: '25', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      showSetRightdialogVisible: false,
      // 
      rightsList: [],
      // 
      treeProps: {
        // 
        label: 'authName',
        // 
        children: 'children'
      },
      // ID
      defKeys: [],
      // Id
      roleId: ''
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList () {
    // 
      const { data: res } = await this.$http.get('roles')
      // 200
      // 200 return   
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 200 roleList
      this.roleList = res.data
      console.log(this.roleList)
    },
    // 
    addDialogClose () {
      this.$refs.addFormRole.resetFields()
    },
    // 
    async addRole () {
      // 
      const { data: res } = await this.$http.post('roles', this.addForm)
      console.log(res.meta.status)
      // 
      if (res.meta.status !== 201) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.addroleDialogVisible = false
      // 
      this.addDialogClose()
      this.getRolesList()
      this.roleList = res.data
    },
    // ID
    async selectRoleId (id) {
      const { data: res } = await this.$http.get('roles/')
      console.log(res)
    },
    //  
    editDialogClose () {
      this.$refs.editFormRef.resetFields()
    },
    //  input
    async showEditDialog (id) {
      // 
      this.editDialogVisible = true
      // 
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.editForm = res.data
    },
    // 
    async editRoleInfo () {
      // 
      const { data: res } = await this.$http.put('roles/' + this.editForm.roleId, {
        roleName: this.editForm.roleName,
        roleDesc: this.editForm.roleDesc
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      this.editDialogVisible = false
      // 
      this.getRolesList()
    },
    // ID
    async deleteEditDialog (id) {
      // 
      const res = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
        // catch,
      }).catch(err => err)
      //  confirm
      //  cancel
      console.log(res)
      if (res !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: result } = await this.$http.delete('roles/' + id)
      if (result.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      this.getRolesList()
    },
    // ID
    // role.id ,id rightId ID
    async removeRightById (role, rightId) {
    // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      // , 
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      //   ID ID
      const { data: res } = await this.$http.delete(`roles/${role.id}}/rights/${rightId}}`)
      // (status) 200  200  
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //  ()
      // this.getRolesList()
      // 
      // 
      role.children = res.data
      this.defKeys = rightId
      console.log(rightId)
    },
    // 
    async showSetRightDialog (role) {
      this.roleId = role.id
      // ID
      this.getLeafKeys(role, this.defKeys)
      // 
      this.showSetRightdialogVisible = true
      // 
      const { data: res } = await this.$http.get('rights/tree')
      // 200
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      // console.log(res)
      // 200  (rightsList)
      this.rightsList = res.data
    },
    // 
    getLeafKeys (node, arr) {
      //  children
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    // 
    SetRightDialogClosed () {
      // 
      this.defKeys = []
    },
    // 
    async allotRights () {
      // id
      const keys = [
        // ID
        ...this.$refs.treeRef.getCheckedKeys(),
        // ID
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      // 
      const idStr = keys.join(',')
      console.log(idStr)
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      this.getRolesList()
      // 
      this.showSetRightdialogVisible = false
    }

  }
}
</script>
<style lang="less" scoped>
.el-tag{
  margin:7px;
}
.bdtop{
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
</style>
