<template>
  <div class="login_container">
   <div class="login_box">
    <!---->
     <div class="avatar_box">
       <img src="../assets/logo.png">
     </div>
     <div class="login_size">
       <h2></h2>
     </div>
    <!---->
     <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" class="login_form">
        <!---->
       <el-form-item prop="username">
         <el-input  v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
       </el-form-item>
       <!---->
       <el-form-item prop="password">
         <!--type="password"-->
         <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" type="password"></el-input>
       </el-form-item>
        <!---->
       <el-form-item class="btns">
         <el-button type="primary" @click="login"></el-button>
         <el-button type="info" @click="resetLoginForm"></el-button>
       </el-form-item>
     </el-form>
   </div>
  </div>
</template>

<script>
/* required */
export default {
  data () {
    return {
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 
      loginFormRules: {
        // 
        // trigger ,
        username: [{ required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }],
        // 
        password: [{ required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }]
      }

    }
  },
  methods: {
    resetLoginForm () {
      // console.log(this)
      this.$refs.loginFormRef.resetFields()
    },
    login () {
      //  ,
      this.$refs.loginFormRef.validate(async valid => {
        // eslint-disable-next-line no-useless-return
        if (!valid) return
        // eslint-disable-next-line no-undef,no-unused-vars
        const { data: res } = await this.$http.post('login', this.loginForm)
        //  this.message.error
        // eslint-disable-next-line eqeqeq
        if (res.meta.status != 200) return this.$message.error('')
        this.$message.success('')
        // 1.token,sessionStorage
        // 1.1API
        // 1.2 token ,tokensessionStorage
        console.log(res)
        // tokensessionStorage
        window.sessionStorage.setItem('token', res.data.token)
        // 2., /home
        this.$router.push('/home')
      })
    }
  }
}
</script>
